// ADAPTIVE MENU
.adaptive-menu {
    position: fixed;
    opacity: 0;
    visibility: hidden; 
    pointer-events: none;
    z-index: 100;
    top: 0;
    left: 0;
    display: flex;
    width: 100%;
    height: 100%;
    // background-color:;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    margin: auto;
    overflow-y: auto;
    overflow-x: hidden;
    pointer-events: none;
}

.adaptive-menu.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
}

.adaptive-menu {
overflow: hidden;
display: block;
width: 100vh;
height: 100vh;
background-color: #ffffff;
margin: 0 auto;
padding-top: 33px;
background-image: url(../images/main_menu/decor_mobile.png);
}

@media screen and (min-width: 320px) {
.adaptive-menu {
width: 320px;
height: 568px;
padding-left: 20px;
padding-right: 20px;

}

}
@media screen and (min-width: 768px) {
.adaptive-menu {
width: 768px;
height: 1024px;
padding-left: 194px;
padding-right: 194px;
/* background-image:
url(../images/main_menu/decor_tablet.png),url(../images/main_menu/Main_menu_tablet1.jpg),url(../images/main_menu/Main_menu_tablet2.jpg),
url(../images/main_menu/Main_menu_tablet3.jpg),url(../images/main_menu/Main_menu_tablet4.jpg); */
background-repeat: no-repeat;
background-position: center,left 106px top 79px,right 32px top 79px,left 32px top 364px,right 32px top 303px;
}

}
@media screen and (min-width: 1440px) {
.adaptive-menu {
display: none;
}
}


