font-size: 24px;
line-height: 1.26;
margin: 0 auto;
color: var(--secondary-text-color);
/* &:hover,
&:focus {
fill: #ffffff;
text-shadow: -1px 0 #487996, 0 1px #487996, 1px 0 #487996, 0 -1px #487996;
} */
}
.adaptive__link:hover,.adaptive__link:focus {
color: #ffffff;
text-shadow: -1px 0 #487996, 0 1px #487996, 1px 0 #487996, 0 -1px #487996;
}

.adaptive-menu__link {
display: flex;
align-items: center;
justify-content: center;
font-family: Lato;
font-weight: 500;
font-size: 24px;
line-height: 1.26;
margin: 0 auto;
color: var(--secondary-text-color);
/* &:hover,
&:focus {
fill: var(--button-bg);
transition: color var(--animation);
} */
}
.adaptive-menu__link:hover,.adaptive-menu__link:focus {
color: #ffffff;
text-shadow: -1px 0 #487996, 0 1px #487996, 1px 0 #487996, 0 -1px #487996;
}

.adaptive-menu__link::before {
display: inline-block;
content: "";
width: 7px;
height: 7px;
align-items: center;
margin-right: 12px;
background-image: url("../images/main_menu/bullet.png");
}

.adaptive-menu__main,
.adaptive__main:not(:last-child) {
margin-bottom: 24px;
}
.adaptive-item:hover::after {
display: block;
content: "";
width: 100%;
height: 2px;
background-color: #2196f3;
margin: -5px auto 0;
}
.adaptive-menu__list {
display: block;
margin: 0 auto;
padding: 0;

}